"use client";
import { useRouter } from "next/navigation";
import { Box, Button, Typography, Breadcrumbs, Link } from "@mui/material";
import { useState } from "react";
import { motion } from "framer-motion";
import SaveIcon from "@mui/icons-material/Save";
import CustomForm from "@/app/components/CustomForm";
import { USER_FORM_FIELDS } from "@/app/components/user/userFormFields";

const AddUser = () => {
  const router = useRouter();
  const [form, setForm] = useState(
    USER_FORM_FIELDS.reduce((acc, tab) => {
      tab.sections.forEach((section) => {
        section.fields.forEach((field) => {
          acc[field.key] = field.type === "multiselect" ? [] : "";
        });
      });
      return acc;
    }, {})
  );

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSave = () => {
    console.log("Form Data:", form);
  };

  const handleBack = () => {
    router.back();
  };

  return (
    <motion.div
      initial={{ opacity: 0, scale: 0.95, y: 10 }}
      animate={{ opacity: 1, scale: 1, y: 0 }}
      exit={{ opacity: 0, scale: 0.95, y: 10 }}
      transition={{ duration: 0.6, ease: [0.4, 0, 0.2, 1] }}
    >
      <Box sx={{ p: 4, bgcolor: "#f9fafb", minHeight: "100vh" }}>
        {/* Breadcrumbs */}
        <Breadcrumbs
          aria-label="breadcrumb"
          sx={{ mb: 3, fontSize: "0.875rem", color: "#666" }}
        >
          <Link underline="hover" color="inherit" href="#">
            Users
          </Link>
          <Typography color="text.primary" sx={{ fontWeight: 600 }}>
            Add User
          </Typography>
        </Breadcrumbs>

        {/* Header + Save Button */}
        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            mb: 4,
          }}
        >
          <Box>
            <Typography variant="h5" sx={{ fontWeight: 700, mb: 1 }}>
              Add User
            </Typography>
            <Typography variant="body2" sx={{ color: "#666" }}>
              Fill in the details below to add a new user.
            </Typography>
          </Box>

          {/* Sticky Save Button */}
          <Button
            variant="contained"
            startIcon={<SaveIcon />}
            onClick={handleSave}
            sx={{
              borderRadius: 2,
              textTransform: "none",
              background: "linear-gradient(to right, #7e5bef, #00c6ff)",
              color: "#fff",
              "&:hover": {
                background: "linear-gradient(to right, #6a50d4, #00a5dd)",
              },
              height: 40,
              fontWeight: 600,
            }}
          >
            Save
          </Button>
        </Box>

        {/* Form Container */}
        <Box
          sx={{
            maxWidth: 700,
            bgcolor: "#fff",
            borderRadius: 2,
            boxShadow: "0 2px 10px rgba(0,0,0,0.04)",
            p: 3,
          }}
        >
          <CustomForm formSchema={USER_FORM_FIELDS} formData={form} onChange={handleChange} />
        </Box>
      </Box>
    </motion.div>
  );
};

export default AddUser;
